














































































































import uniDrawer from '../../../components/uni-drawer.vue';
import uniIcon from '../../../components/uni-icon.vue';
export default {
	components: {
		uniDrawer,
		uniIcon
	},
	data() {
		return {
			rightDrawerVisible: false,
			listData: [],
			nextPage:"",
			page: 1,
			reLoad:true,
			searchData:{},
			getting:false
		}
	},
	methods: {
		formSubmit: function (e){
			this.reLoad = true;
			var formData = e.detail.value;
			if (formData.status=="true"){
				formData.status=true;
			}
				else{
					formData.status=false;
				}
				for (var val in formData){
					//耗时一个下午的重大发现，数组与json用法的区别，如何遍历两者，如何在
					//vue中动态添加遍历并添加json数据
					this.$set(this.searchData,val,formData[val]);
				}
			console.log(e);
			console.log(this.searchData.status+this.searchData.country);
			
			this.getList();
			
			
		},
		formReset: function (e) {
			console.log("清空数据")
			this.chosen = ''
		},
		closeRightDrawer() {
			this.chosen='';
			this.rightDrawerVisible = false;
			
		},
		getList() {
			//防止多次点击getList
			if(this.getting){
				return;
			}
			this.getting=true;
			
		
			let url= 'https://api.thinker.ink/v1/books/';
			if (!this.reLoad){
				
				url=this.nextPage; 
				if(url==''){
					uni.showToast({
						icon:'none',
						title: '无更多数据',
						mask: false,
						duration: 1500
					});
					return;
				}
			}
			
			console.log(url);
			uni.showToast({
				icon:'loading'
			});
			uni.request({
				url: url,
				method: 'GET',
				data: this.searchData,
				success: res => {
					console.log(res.data.count);
					if (res.data.count==0){
						uni.showToast({
							
							title: '没有数据',
							mask: false,
							duration: 1500
						});
					}
					console.log(res);
					if (!this.reLoad) {
						res.data.data.forEach((news) => {
							this.listData= this.listData.concat(res.data.data);
							console.log(this.listData);
							
						});
					}else{
					this.listData= res.data.data;
					}
					this.nextPage=res.data.next;
					this.reLoad=false;
					console.log(this.nextPage);
					console.log(this.listData[0].images[0]);
				},
				fail: () => {},
				complete: () => {}
			});
			this.getting=false;
			//uni.hideLoading();
			this.searchData={};
		},
		
		confirm: function (e) {
				this.reLoad = true;
				this.searchData.search = e.detail.value;
		        console.log(this.searchData);
			uni.showToast({
				icon:'loading',
				title: '搜索'
			});
			this.getList();
			
		}
	},
	onNavigationBarButtonTap(e) {
		console.log("tab顶部键"+e.index);
		switch (e.index){
			case 2:
				console.log("点了评分");
				this.searchData.ordering="level";
				this.getList();
				break;
			case 1:
				console.log("点了距离");
				this.searchData.ordering="place";
				this.getList();
				break;
			case 0:
			this.rightDrawerVisible = !this.rightDrawerVisible;
				break;
			default:
				break;
		}
		
	},
	onBackPress() {
		// 返回按钮监听
		if (this.rightDrawerVisible) {
			this.rightDrawerVisible = false;
			return true;
		}
	},
	onLoad() {
		this.getList();
	},
	onReachBottom() {
		this.reLoad=false;
		this.getList();
		console.log('bottom');
	},
	onPullDownRefresh() {
		this.reLoad=true;
		this.getList();
		uni.stopPullDownRefresh();
	}
}
